<template>
  <g-body-layout
    :pageIndex="pageNumber"
    :pageSize="pageSize"
    :total="total"
    :filterText.sync="filterText"
    @pageIndexChange="pageIndexChange"
    @pageSizeChange="pageSizeChange"
    @suffixSearchCallback="onFilterReady"
  >
    <!-- 按钮组插槽 -->
    <template #btnGroup>
      <g-btn-group :buttonLists="btnList" @btnCallBack="btnGroupClick">
      </g-btn-group>
    </template>
    <!-- g-ndo-table插槽 -->
    <template #table>
      <g-ndo-table
        :id="id"
        :handName="'产品BOM'"
        :dataList="dataList"
        :isTableLoading="loading"
        :total="total"
        @rowClick="rowClick"
      ></g-ndo-table>
    </template>
    <!-- 详细内容插槽 -->
    <template #content>
      <create-or-edit-product-bom
        :ndoEditParams="ndoEditParams"
        :refreshTag="refreshTag"
        :saveTag="saveTag"
        :resetTag="resetTag"
        @childRefresh="childRefresh($event)"
        @rowClick="rowClick($event)"
        v-if="isShowEdit"
      ></create-or-edit-product-bom>
      <g-empty v-else> </g-empty>
    </template>
  </g-body-layout>
</template>

<script>
import { GNdoPageBase } from "@/utils";
import CreateOrEditProductBom from "./create-or-edit-product-bom/create-or-edit-product-bom.vue";

export default {
  name: "ProductBom",
  mixins: [GNdoPageBase],
  components: {
    CreateOrEditProductBom,
  },
  data() {
    return {
      filterText: "",
      isTableLoading: false,
      dataSource: [],
      columns: [
        {
          title: "BOM编号",
          dataIndex: "bomCode",
          width: 120,
        },
        {
          title: "产品编码",
          dataIndex: "productCode",
          width: 120,
        },
        {
          title: "产品名称",
          dataIndex: "productName",
          width: 150,
        },
        {
          title: "BOM版本",
          dataIndex: "version",
          width: 100,
        },
        {
          title: "物料数量",
          dataIndex: "materialCount",
          width: 100,
        },
        {
          title: "状态",
          dataIndex: "status",
          width: 100,
          scopedSlots: { customRender: "status" },
        },
        {
          title: "生效日期",
          dataIndex: "effectiveDate",
          width: 120,
        },
        {
          title: "备注",
          dataIndex: "remark",
        },
        {
          title: "创建人",
          dataIndex: "createdBy",
          width: 100,
        },
        {
          title: "创建时间",
          dataIndex: "createdTime",
          width: 150,
        },
      ],
   
      buttonList: [],
    };
  },
  created() {
    this.getBtnList();
  },
  mounted() {
  },
  methods: {
    getBtnList() {
      this.btnList = [
      {
          text: this.AppConst.button.create,
          icon: this.AppConst.icon.create,
          eventName: this.AppConst.button.create,
          display: true,
        },
        {
          text: this.AppConst.button.copy,
          icon: this.AppConst.icon.copy,
          eventName: this.AppConst.button.copy,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.delete,
          icon: this.AppConst.icon.delete,
          eventName: this.AppConst.button.delete,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.refresh,
          icon: this.AppConst.icon.refresh,
          eventName: this.AppConst.button.refresh,
          right: true,
          display: this.isShowEdit,
        },
        {
          text: this.AppConst.button.save,
          icon: this.AppConst.icon.save,
          eventName: this.AppConst.button.save,
          right: true,
          type: "primary",
          display: this.isShowEdit,
        },
      ];
    },
    handleButtonClick(key) {
      switch (key) {
        case "add":
          this.handleAdd();
          break;
        case "edit":
          this.handleEdit();
          break;
        case "delete":
          this.handleDelete();
          break;
        case "enable":
          this.handleStatusChange("启用");
          break;
        case "disable":
          this.handleStatusChange("停用");
          break;
      }
    },
    handleAdd() {
      this.$router.push({
        name: "CreateOrEditProductBom",
      });
    },
    handleEdit() {
      const selectedRow = this.selectedRows[0];
      this.$router.push({
        name: "CreateOrEditProductBom",
        query: { id: selectedRow.id },
      });
    },
    handleDelete() {
      const selectedIds = this.selectedRows.map((row) => row.id);
      this.$confirm({
        title: "确认删除",
        content: "是否确认删除选中的BOM记录？",
        onOk: () => {
          // 模拟删除操作
          console.log("删除的ID：", selectedIds);
          this.fetchData();
        },
      });
    },
    handleStatusChange(status) {
      const selectedIds = this.selectedRows.map((row) => row.id);
      this.$confirm({
        title: `确认${status}`,
        content: `是否确认${status}选中的BOM记录？`,
        onOk: () => {
          // 模拟状态更新操作
          console.log("更新状态的ID：", selectedIds, status);
          this.fetchData();
        },
      });
    },
    onSearch() {
      this.fetchData();
    },
    fetchData(request) {
      this.isTableLoading = true;
      // 模拟数据获取
      setTimeout(() => {
        request.finishedCallback();
        let dataList = [];
        for (let i = 0; i < 10; i++) {
            dataList.push({
            id: i + 1,
            bomCode: `BOM${String(i + 1).padStart(4, "0")}`,
            productCode: `P${String(i + 1).padStart(4, "0")}`,
            name: `产品${i + 1}`,
            version: `V1.${i}`,
            materialCount: Math.floor(Math.random() * 20) + 1,
            status: i % 2 === 0 ? "启用" : "停用",
            effectiveDate: "2024-03-15",
            remark: `这是产品${i + 1}的BOM说明`,
            createdBy: "管理员",
            createdTime: "2024-03-15 12:00:00",
          });
        }

      
        request.successCallback({
          items: dataList,
          totalCount: dataList.length * 2,
        });
      }, 1000);
    },
    handleTableChange(pagination) {
      this.fetchData();
    },
    handleSelectionChange(selectedRowKeys, selectedRows) {
      this.selectedRowKeys = selectedRowKeys;
      this.selectedRows = selectedRows;
      
      // 更新按钮状态
      this.buttonList = this.buttonList.map((btn) => {
        if (["edit", "delete"].includes(btn.key)) {
          btn.disabled = selectedRowKeys.length !== 1;
        } else if (["enable", "disable"].includes(btn.key)) {
          btn.disabled = selectedRowKeys.length === 0;
        }
        return btn;
      });
    },
  },
};
</script>

<style lang="scss" scoped>
</style>